body {
  margin: 0;
  font-family: 'inter';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7f6ff;
}

* {
  font-family: 'inter';
}

.wrapper {
  background: #ffffff;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;
}

header {
  border-bottom: 1px solid #f3f3f3;

  img {
    margin-right: 15px;
  }

  h3,
  p {
    margin: 0;
  }
}

.content {
  h1 {
    margin: 0;
  }
}

.search-block {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 0 15px;

  input {
    border: 0;
    padding: 13px;
    font-size: 16px;
    outline: none;
    width: 148px;
  }

  img {
    margin: 10px;
    margin-left: 1px;
    margin-right: 1px;
  }
}

.cardTotalBlock {


  ul {
    margin-bottom: 30px;
  }

  li {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
  }

  div {
    flex: 1;
    border-bottom: 1px dashed #d7d7d7;
    height: 1px;
    position: relative;
    top: -4px;
    margin: 0 7px;
  }

  .greenButton {
    position: relative;

    &:hover {
      img {
        transform: translateX(5px);
      }
    }


    img {
      position: absolute;
      right: 30px;
      top: 20px;
      transition: transform 0.15 ease-in-out;
    }
  }


  .greenButton {
    width: 100%;
    position: absolute;
    width: 385px;
    height: 55px;
    background: #9DD558;
    border-radius: 18px;
    cursor: pointer;
    border: 0;
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    transition: background 0.2s ease-in-out;

    &:hover {
      background: lighten(#9DD558, 5%);
    }

    &:active {
      background: darken(#9DD558, 5%);
    }
  }
}

.cardItem {
  border: 1px solid #f3f3f3;
  border-radius: 20px;
  overflow: hidden;



  .removeBtn {
    opacity: 0.3;
    transition: opacity 0.2s ease-in-out;
    cursor: pointer;


    &:hover {
      opacity: 1;
    }
  }
}